import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import { StyledContainer } from '../style'
import { Link } from 'gatsby-plugin-react-i18next'

import {
  Wrapper,
  StyledImg,
  Menu,
  MenuImg,
  MenuWrapper,
  MenuBody,
  MenuItem,
  Separator,
} from './style'

const Header = ({ handleWhoare, handleDo }) => {
  const { t } = useTranslation()
  const [open, setOpen] = useState(false)
  const openMenu = () => {
    setOpen(!open)
  }
  return (
    <Wrapper>
      <StyledContainer>
        <MenuWrapper>
          <StyledImg alt='logo' src='../../images/logo-dark.png' />
          <Menu>
            <MenuImg
              onClick={() => openMenu()}
              src='../../images/menu.svg'
              alt='menu'
            />
            {open && (
              <MenuBody>
                <MenuItem>
                  <a href='https://warm-dawn-48762.herokuapp.com/users/sign_in'>
                    {t('Ingresar')}
                  </a>
                </MenuItem>
                <Separator />
                <Link to='/proyecto'>
                  <MenuItem>{t('Ecoproyectos')}</MenuItem>
                </Link>
                <Separator />
                <MenuItem onClick={handleWhoare}>{t('Quiénes somos')}</MenuItem>
                <MenuItem onClick={handleDo}>{t('Qué hacemos')} </MenuItem>
                <MenuItem>{t('Cómo funciona')} </MenuItem>
                <Separator />
                <MenuItem>{t('Aprender')} </MenuItem>
              </MenuBody>
            )}
          </Menu>
        </MenuWrapper>
      </StyledContainer>
    </Wrapper>
  )
}

export default Header
