import React from 'react'
import { Link } from 'gatsby'

import { CardContainer, Body, Header, Section, Action } from './style'
import { useTranslation } from 'react-i18next'
const Card = ({
  title,
  img,
  text,
  tipo,
  tipoText,
  reduccion,
  cumplimiento,
  costoMilla,
  estandar,
  url
}) => {
  const { t } = useTranslation()

  return (
    <CardContainer>
      <Header color={tipo === 1 ? '#7E6EAD' : '#2A9274'}>
        <div style={{ width: '90%' }}>
          <h1>{title}</h1>
        </div>
        <div>
          {tipo === 1 && <img src='../../images/tipo1.png' alt='img' />}
          {tipo === 2 && <img src='../../images/tipo2.svg' alt='img' />}
        </div>
      </Header>
      <Body>
        <img src={`../../images/${img}`} alt='image' />
        <h1>{text}</h1>
        <Section>
          {t('Tipo:')} <span>{tipoText}</span>
        </Section>
        <Section>
          {t('Reducción anual estimada')}: <span>{reduccion}</span>
        </Section>
        <Section>
          <div className='corsia'>
            {t('Cumplimiento CORSIA')}:{' '}
            <span>
              {cumplimiento}
            </span>
          </div>
        </Section>
        <Section>
          {' '}
          {t('Estándar')}:<span> {estandar}</span>
        </Section>
        <Link to={url}>
          <Action color={tipo === 1 ? '#7E6EAD' : '#2A9274'}>
            {t('Conocer más')}
          </Action>
        </Link>
      </Body>
    </CardContainer>
  )
}

export default Card
